import { Box, Button, Dialog, DialogActions, DialogContent, DialogTitle, Divider, IconButton, styled } from '@mui/material'
import React, { FC } from 'react'
import CloseIcon from '@mui/icons-material/Close';

type Props = {
    onClickDialog: React.Dispatch<React.SetStateAction<boolean>>,
    openDialog: boolean,
    onClickDelete: () => void,
}

const DeleteCardItemDialog: FC<Props> = ({ onClickDialog, openDialog, onClickDelete }) => {
    const handleClose = () => {
        onClickDialog(false);
    };

    const CustomDialog = styled(Dialog)(({ theme }) => ({
        '& .MuiDialogContent-root': {
            padding: theme.spacing(2),
        },
        '& .MuiDialogActions-root': {
            padding: theme.spacing(2),
        },
    }));

    return (
        <>
            <CustomDialog
                open={openDialog}
                onClose={handleClose}
                sx={{
                    "& .MuiDialog-container": {
                        "& .MuiPaper-root": {
                            width: "100%",
                            maxWidth: "350px",  // Set your width here
                        },
                    },
                }}>
                <Box position='relative'>
                    <DialogTitle>
                         
                    </DialogTitle>
                    <IconButton
                        aria-label="delete"
                        size="small"
                        onClick={handleClose}
                        sx={{
                            position: 'absolute',
                            top: '50%',
                            right: 16,
                            transform: 'translateY(-50%)'
                        }}>
                        <CloseIcon fontSize="medium" />
                    </IconButton>
                </Box>
                <Divider />
                <DialogContent>
                      ?    .
                </DialogContent>
                <DialogActions>
                    <Box display='flex' justifyContent='space-between' flexGrow={1}>
                        <Button
                            variant='contained'
                            color='error'
                            onClick={onClickDelete}
                            sx={{ flexGrow: 1, marginRight: 2 }}>
                            
                        </Button>
                        <Button
                            variant='outlined'
                            onClick={handleClose}
                            sx={{ flexGrow: 1 }}>
                            
                        </Button>
                    </Box>
                </DialogActions>
            </CustomDialog>
        </>
    )
}

export default DeleteCardItemDialog;